/* add styles here */
@font-face {
  font-family: Avenir-book;
  src: url(/src/fonts/Avenir-Book.ttf);
}

@font-face {
  font-family: Avenir-heave;
  src: url(/src/fonts/Avenir-Heavy.ttf);
}

* {
  box-sizing: border-box;
}

input {
  outline: none;
  color: #3d4e61;
  width: 100%;
}



/* #big form */
.form__big {
  margin: 20px 0;
}

.form__big--container {
  width: 100%;
  position: relative;
  height: 70px;
}

.form__big--input {
  width: 100%;
  background: #fff;
  border: 1px solid rgba(225, 231, 237, 1);
  box-shadow: 0 1px 8px 0 rgba(61, 78, 97, 0.1);
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 62px;
  border-radius: 4px;

  font-size: 16px;
  line-height: 1.36;
  font-family: Avenir-book, sans-serif;

  transition: box-shadow 0.2s ease-in-out;
}

.form__big--container::after {
  content: '';
  display: block;
  background-image: url(/src/images/Search.svg);
  width: 19px;
  height: 19px;
  position: absolute;
  left: 24px;
  top: 50%;
  transform: translateY(-50%);
}

.form__big--input::placeholder {
  color: #3d4e61;
}

.form__big--input:hover,
.form__smal--input:hover {
  border: 1px solid #e1e7ed;
  box-shadow: 0 3px 8px 0 #3d4e6133;
}

.form__big--input:focus {
  background: linear-gradient(180deg, #fff 0%, #f6f6f7 100%);
  box-shadow: 0 4px 4px 0 #00000040;
  border: 1px solid #e1e7ed;
  font-family: Avenir-heavy, sans-serif;
}

/* #end big form */
.form__small--container {
  position: relative;
  height: 42px;
}

.form__small--input {
  width: 100%;
  background: #fff;
  box-shadow: 0 1px 8px 0 #3d4e611a;
  border: 1px solid #e1e7ed;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 33px;
  border-radius: 4px;

  font-size: 14px;
  line-height: 1.36;
  font-family: Avenir-book, sans-serif;

  transition: box-shadow 0.2s ease-in-out;
}

.form__small--container::after {
  content: '';
  display: block;
  background-image: url(/src/images/Search.svg);
  background-size: cover;
  width: 11px;
  height: 11px;
  position: absolute;
  left: 13px;
  top: 50%;
  transform: translateY(-50%);
}

.form__small--input::placeholder {
  color: #3d4e61;
}

.form__small--input:focus {
  background: linear-gradient(180deg, #fff 0%, #f6f6f7 100%);
  border: 1px solid #e1e7ed;
  font-family: Avenir-heavy, sans-serif;
}
